

  <template>
  <div>
    <v-sheet class="mx-auto mt-5 mb-5" elevation="8" max-width="800">
      <h1>Activities</h1>
      <v-expansion-panels :value="active_tab">
        <v-expansion-panel v-for="activity in activities" :key="activity.title">
          <v-expansion-panel-header disable-icon-rotate>
            <h2>{{ activity.title }}</h2>
            <template v-slot:actions>
              <v-icon color="teal">{{ activity.icon }}</v-icon>
            </template>
          </v-expansion-panel-header>
          <v-expansion-panel-content>
            {{
            activity.description
            }}
          </v-expansion-panel-content>
        </v-expansion-panel>
        <v-expansion-panel>
          <v-expansion-panel-header disable-icon-rotate>
            <h2>Rentals & Fees</h2>
            <template v-slot:actions>
              <v-icon color="teal">mdi-tag</v-icon>
            </template>
          </v-expansion-panel-header>
          <v-expansion-panel-content>
            <v-simple-table dense>
              <template v-slot:default>
                <thead>
                  <tr>
                    <th class="text-left">Name</th>
                    <th class="text-center">Details</th>
                    <th class="text-center">Sizes</th>
                    <th class="text-center">Cost</th>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="item in pricing" :key="item.name">
                    <td>{{ item.name }}</td>
                    <td>{{ item.details }}</td>
                    <td>{{ item.sizes }}</td>
                    <td>{{ item.regularcost }}</td>
                  </tr>
                  
                </tbody>
              </template>
            </v-simple-table>
            <h3 align="center">NO Trail Fees! Donations Appreciated</h3>
          </v-expansion-panel-content>
        </v-expansion-panel>
      </v-expansion-panels>
    </v-sheet>
    <br />
    <br />
    <br />
  </div>
</template>

<script>
export default {
  data: () => ({
    model: null,
    activities: [
      {
        title: "Skiing",
        description:
          "Cross Country skiing has always been at the heart of the Outdoor Center. In a purposeful departure from the busy, manicured trails that have become the norm, the Outdoor Center offers the serenity of quiet, ungroomed, and authentic backcountry adventures, followed by the coziness of home with our hot soup and cookies waiting for you upon your return. Ski rentals (adult sizes only) are available at the Outdoor Center.",
        icon: "mdi-ski"
      },
      {
        title: "Snowshoeing",
        description:
          "Getting out in to the snowy woods on snowshoes is something nearly everyone can enjoy together. All our trails can be used for snowshoeing and it is easy for the more adventurous to go off trail to explore in our open woods! Rental snowshoes are available in the Outdoor Center.",
        icon: "mdi-snowflake"
      },
      {
        title: "Sledding",
        description:
          "We have a small hill for sledding. Kids love it! Bring your own sleds. It is located on the inn side of the road.",
        icon: "mdi-slide"
      },
      {
        title: "Hiking",
        description:
          "The Inn and Outdoor Center sit in the center of nearly endless hiking trails for all abilities. Many of our trails are double wide and grassy; venturing onto Moosalamoo National Recreation Area trails will bring you to rockier single track trails. Check out a few of our favorite trails here, and be sure to download a map before you head out! For hiking, we find that most of our trails can be completed in sturdy sneakers, but boots are never a bad idea, especially if you are heading further afield to the long trail.",
        icon: "mdi-hiking"
      },
      {
        title: "Biking",
        description:
          "The BBH Outdoor Center is located amongst some of the best mountain biking trails and roads in Vermont. The extensive network of dirt Forest Service roads, remote and rarely traveled by motorized vehicles, offer many miles of beginning mountain biking. More experienced bikers can utilize the winding single-track trails that twist and turn throughout the mountains and hills. Chandler Ridge, Leicester Hollow, and Oak Ridge trails, which offer views of Moosalamoo mountain, Lake Dunmore, and Silver Lake, are perennial favorites for many bikers, and have recently undergone extensive maintenance to make for a perfect rugged biking adventure! Green Mountain Bikes in Rochester and Frog Hollow Bikes in Middlebury both offer bike rentals!",
        icon: "mdi-bike"
      },
      {
        title: "Trail Running",
        description:
          "Our network of trails is paradise for trail and mountain runners of all abilities. As the starting point for a number of trail races including the Moosalamoo Ultra, the Endurance Society Infinitus Races, and our own Goshen Gallop 5/10km trail race, the Outdoor Center offers a jumping off point for your training run of any length. Run 50 miles without repeating a single trail! A number of trails cross roads and access points, perfect for a midday gear-drop.",
        icon: "mdi-run"
      }
    ],
    pricing: [
      {
        name: "Ski Rentals - Full Day  AVAILABILITY EXTREMELY LIMITED DUE TO DELAY IN SHIPPING. PLEASE BRING YOUR OWN SKIS OR BE EXCITED ABOUT SNOWSHOEING IF WE CAN NOT OUTFIT YOU!",
