        sizes: "Adult",
        regularcost: "$26"
      },
      {
        name: "Snowshoe Rentals - Full Day",
        details: "Redfeather snowshoes",
        sizes: "Adult & Kids",
        regularcost: "$19"
      },
      {
        name: "Microspikes - Full Day",
        details: "Microspikes & Ecospikes",
        sizes: "Small - Large",
        regularcost: "$19"
      }
    ]
  }),
  computed: {},
  props: {
    active_tab: {
      type: Number,
      default: -1
    }
  }
